/* ဘယ်က စကြမလဲ?​ */

import { Button, Container, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, Grid, Paper, Stack, Typography } from '@mui/material';
import { deleteCategoryAsyncThunk } from 'features/category/categorySlice';
import { selectCategoryDeleteStatus, selectCategoryError, selectMenuDeleteStatus, selectMenuError, selectSelectedCategory, selectSelectedMenu, setCategoryDeleteStatus, setMenuDeleteStatus, setSelectedItem } from 'features/main/mainSlice';
import { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { Link, useParams } from 'react-router-dom';
import Iconify from 'template/components/iconify';
import { useDispatch } from 'react-redux';
import { selectAccessToken } from 'features/user/userSlice';
import { useRouter } from 'template/routes/hooks';
import { deleteMenuAsyncThunk } from 'features/menu/menuSlice';

/*
    State တွေ စယူမလား?
    UI အရင်ချတာ ပိုကောင်းမယ်။

    

*/
export default function MenuDetailView() {
  // ---- Core Data -------- //

  const { id } = useParams();
  const dispatch = useDispatch();
  const router = useRouter();

  // လိုတဲ့ data နှစ်ခု က
  // selected category

  const selectedMenu = useSelector(selectSelectedMenu);
  const deleteStatus = useSelector(selectMenuDeleteStatus);
  const accessToken = useSelector(selectAccessToken);
  const menuError = useSelector(selectMenuError);

  // ---------- internal state ------------ //
  // for delete confirmation dialog
  const [open, setOpen] = useState(false);

  function handleItemCardClick(item) {
    dispatch(setSelectedItem(item));
    router.push(`/items/${item.id}/detail`);
  }


  const handleClose = (id, access_token) => {
    console.log('id is ' + id);
    console.log('access_token is ' + access_token);
    if (id !== 0) {
      // dispatch delete shop api
      let formData = new FormData();
      formData.append('access_token', access_token);
      formData.append('id', id);
      dispatch(deleteMenuAsyncThunk(formData));
      //dispatch(deleteShopAsyncThunk(formData));
    }
    setOpen(false);
  };

  const handleErrorDialogClose = () => {
    dispatch(setMenuDeleteStatus('idle'));
  };


  useEffect (() => {
    if(deleteStatus === "succeeded"){
      // set back to idle 
      dispatch(setCategoryDeleteStatus("idle"));
      // and then go back to shops detail 
      router.back();
      
    }
  }, [deleteStatus]); // Include history as a dependency
  

  // ----------------------- UI ------------------------- //

  const renderShopDetailSection = (
    // this is right section of shop detail
    <>
      <Grid item xs={12} sm={6} style={{}}>
        <Stack direction="column" spacing={3}>
          <Stack direction="row" spacing={3}>
            <Typography>{selectedMenu.name}</Typography>
          </Stack>
          <Stack direction="row" spacing={3}>
            <Typography>{selectedMenu.description}</Typography>
          </Stack>
          <Button
            component={Link}
            to={`/menus/${id}/edit`}
            variant="contained"
            color="inherit"
            startIcon={<Iconify icon="eva:plus-fill" />}
          >
            Edit Menu
          </Button>
          {/* loading button for delete */}
          Menu Delete Status : {deleteStatus}
          <Button
            onClick={() => setOpen(true)}
            variant="contained"
            color="inherit"
            startIcon={<Iconify icon="eva:plus-fill" />}
          >
            Delete Menu
          </Button>
        </Stack>
      </Grid>
    </>
  );

  return (
    <>
      <Container sx={{ mb: 3 }}>
        {/* image , side by side , list of text  */}
        <Grid container spacing={2}>
          <Grid item xs={12} sm={6} style={{}}>
            {/* stack two image */}
            <img
              src={selectedMenu.background_image_url}
              alt={selectedMenu.name}
              loading="lazy"
            />
          </Grid>
          {renderShopDetailSection}
        </Grid>
      </Container>

      <Container spacing={3} sx={{ mt: 3 }}>
        <Stack
          spacing={3}
          direction="row"
          alignItems="center"
          justifyContent="space-between"
          mb={5}
        >
          <Typography variant="h4">Items</Typography>

          <Button
            component={Link}
            to="/items/create"
            variant="contained"
            color="inherit"
            startIcon={<Iconify icon="eva:plus-fill" />}
          >
            New Item
          </Button>
        </Stack>

        <Grid container spacing={3} sx={{ justifyContent: 'flex-start' }}>
          {selectedMenu.items.map((item) => (
            <Grid item xs={12} sm={6} md={4} key={item.id} sx={{ mb: 3 }}>
              <Container onClick={() => handleItemCardClick(item)} sx={{ p: 0, m: 0 }}>
                <Paper elevation={3} style={{ padding: '16px', cursor: 'pointer' }}>
                  <Typography variant="h6" sx={{ p: 0, m: 0 }}>
                    {item.name}
                  </Typography>
                  <Typography variant="body2">{item.description}</Typography>
                </Paper>
              </Container>
            </Grid>
          ))}
        </Grid>
      </Container>



      {/* delete confirmation dialog */}
      <Dialog
        open={open}
        // onClose={() => handleClose(id)}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">Delete Confirmation!</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Are you sure to delete this menu info?
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => handleClose(0)} autoFocus>
            Cancel
          </Button>
          <Button onClick={() => handleClose(id, accessToken)} autoFocus>
            OK
          </Button>
        </DialogActions>
      </Dialog>

      {/* delete error dialog */}
      <Dialog
        open={deleteStatus === 'error'}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">Error!</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">{menuError}</DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleErrorDialogClose} autoFocus>
            OK
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
}
